@import "../../../css/module/fn";
@import "../../../css/module/color";

.mui-content {
	background-color: #494949;
	padding-bottom: p2m(20);
}

#searchWrap {
	height: p2m(90);
	padding: 6px;
	* {
		color: #fff !important;
	}
	
	.mui-search {
	
	}
	input[type='search'] {
		background-color: #696969;
		border: 1px solid #999;
	}
}

#tbSchedule {
	width: 100%;
	table-layout: fixed;
	border: none;
	border-collapse: collapse;
	border-top: 1px solid #fff;
	tr {
		> td {
			width: p2m(206);
			height: p2m(165);
			border: 1px solid #eee;
			text-align: center;
			border-top: none;
			border-right: none;
			
			&:nth-of-type(4n+1) {
				font-size: p2m(30);
				line-height: p2m(50);
				padding: p2m(30);
				width: p2m(130);
				border-left: none;
				color: #999;
				background-color: #494949;
				border-bottom: 1px dashed #999;
				text-transform: uppercase;
			}
		}
	}
	
	thead {
		td {
			color: #fff;
			
			font:{
				weight: 600;
				size: p2m(70);
			}
			
			
			&:nth-of-type(4n+1) {
				font-size: p2m(32);
				font-weight: 300;
			}
			&:nth-of-type(4n+2) {
				background-color: #eb5a32;
			}
			&:nth-of-type(4n+3) {
				background-color: #dd3f31;
			}
			&:nth-of-type(4n) {
				background-color: #d71711;
			}
		}
	}
	
	tbody {
		td {
			background-color: #fff;
			
			&.noTime {
				background-color: #ddd;
				color: #aaa;
			}
		}
		
		&.mine {
			td {
				height: p2m(220);
				
				div{
					width: p2m(190);
					margin: auto;
					border-radius: 5px;
					font-size: p2m(22);
					text-transform: capitalize;
				}
				.divAddTo {
					border: 2px dashed #d71711;
					color: #d71711;
					font-size: p2m(30);
				}
				
				.divAppoint{
					color: #f1d27e;
					border: 1px solid #f1d27e;
					background-color: #fff8df;
				}
				.divActivity{
					color: #fd722d;
					border: 1px solid #fd722d;
					background-color: #ffdece;
				}
				.divMeeting{
					color: #92be57;
					border: 1px solid #92be57;
					background-color: #e5eed7;
				}
				.divWalk{
					color: #617dc3;
					border: 1px solid #617dc3;
					background-color: #d7e3ee;
				}
				
				&.r1{
					div{
						height: p2m(200);
						line-height: p2m(200);
					}
				}
				&.r2{
					div{
						height: p2m(95);
						line-height: p2m(95);
						margin-bottom: 3px;
					}
				}
				&.r3{
					div{
						height: p2m(60);
						line-height: p2m(60);
						margin-bottom: 3px;
					}
				}
				&.r4{
					div{
						height: p2m(44);
						line-height: p2m(44);
						margin-bottom: 3px;
					}
				}
			}
		}
		
		&.rest {
			td {
				height: p2m(60);
				text-transform: uppercase;
				&.lunch {
					background-color: #ddd;
					color: #aaa;
				}
			}
		}
	}
	
}